// initial state
const state = {
  usrInfo: {
    usrBalance:''
  },
  buyBalanceVisable:false
}

// getters
const getters = {}

// actions
const actions = {
  setUrsInfo({ commit },UrsInfo) {
    
    commit('setUrsInfo',UrsInfo)
  },
  setBuyBalanceVisable({ commit },isVisable) {
    
    commit('setBuyBalanceVisable',isVisable)
  }
}

// mutations
const mutations = {
  setUrsInfo(state,val) {
    state.usrInfo = val
  },
  setBuyBalanceVisable(state,val) {
    
    state.buyBalanceVisable = val
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



