import Vue from 'vue'
import Router from 'vue-router'
import Room from '@/assets/pages/Room'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Room',
      component: Room,
    },{
      name:'pokerroom',
      path: '/pokerroom',
      component: () => import('@/components/PokerRoom')
    }
  ]
})



