<template>
  <section v-loading.fullscreen.lock="fullscreenLoading">
    <!-- Dialog -->
    <el-dialog
      id="roomInfoModel"
      width="50%"
      class="roomInfoDialog"
      :title="$t('RoomPage.FeeDialogTitle')"
      :visible.sync="dialogInfo.dialogVisable"
      append-to-body
      :before-close="beforDialogCLose"
    >
      <el-input-number
        v-model="changeSliderModel"
        :min="currentRow.limitFee?~~currentRow.limitFee:''"
        :max="currentRow.maxFee?~~currentRow.maxFee:''"
      ></el-input-number>
      <p>{{limitFeeComputed}}</p>
      <span slot="footer" class="dialog-footer">
        <el-button round class="usrInfoSave" @click="savehandel">{{$t('RoomPage.roomFeeConfirm')}}</el-button>
      </span>
    </el-dialog>
    <!-- Dialog -->
    <el-dialog
      id="lessLimitInfoModel"
      width="50%"
      class="roomInfoDialog"
      :title="$t('RoomPage.lessFeeDialogTitle')"
      :visible.sync="tipDialogInfo.dialogVisable"
      append-to-body
      ref="gameFrame"
    >
      <p style="text-align:center;">
        {{$t('RoomPage.notEnoughtTip',{
        limitFee:`${this.currentRow.limitFee} `,
        selfBalance:~~this.$store.state.userInfo.usrInfo.usrBalance
        })}}
      </p>
      <span slot="footer" class="dialog-footer">
        <el-button
          round
          class="usrInfoSave"
          @click="notEnoughtSavehandel"
        >{{$t('RoomPage.roomFeeBuy')}}</el-button>
      </span>
    </el-dialog>

    <el-row class="gameList" type="flex" justify="space-between" align="middle">
      <el-col
        :span="12"
        :class="['gameItem',gameItem.isDeving?'gameItemWait':'']"
        v-for="(gameItem,gameIdx) of gameList"
        :key="gameIdx"
      >
        <img
          class="gameImg"
          v-bind:src="gameItem.imgSrc"
          :alt="gameItem.label"
          v-if="gameItem.imgSrc"
        >
        <span :class="['gameLabel',gameIdx == 1?'gameWhiteLabel':'']">{{$t(`NavPage.${gameItem.label}`)}}</span>
      </el-col>
    </el-row>
    <el-row>
      <el-table
        v-loading="isRoomListLoading"
        id="roomList"
        class="roomList"
        :default-sort = "{prop: 'roomId', order: 'ascending'}"
        :data="roomDataList"
        height="410"
        style="width: 100%"
        @row-click="rowClickHandle"
      >
        <el-table-column
          align="center"
	  v-if="false"
          prop="roomId"
          :label="$t('NavPage.tableLabelList.roomId')"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="roomName"
          :label="$t('NavPage.tableLabelList.roomId')"
        ></el-table-column>
        <el-table-column align="center" prop="sbAndbb" :label="$t('NavPage.tableLabelList.sb')"></el-table-column>
        <el-table-column
          align="center"
          prop="sizesales"
          :label="$t('NavPage.tableLabelList.sizesales')"
          :formatter="(row)=>{
            return `${row.limitFee}/${row.maxFee}`
          }"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="n"
          :formatter="(row)=>{
            return `${row.playerCount}/${row.totalCount}`
          }"
          :label="$t('NavPage.tableLabelList.playerCount')"
        ></el-table-column>
      </el-table>
    </el-row>
  </section>
</template>
<script>
import numeral from "numeral";
import {
  fetchRoomList,
//  doSetQGameResult,
//   doStartQuickGame,
  fetchAuthentication,
  fetchSignSeed,
  fetchIsonchannel,
  dochecktoken
} from "@/api/poker.js";
import TronPokerApp from "../lib/tron";
export default {
  name: "MainList",
  data() {
    return {
      fullscreenLoading:false,
      winNotComfirmItem: "",
      notComfirmItem: "",
      confirmInt: "",
      notCofirmQueue: [],
      notComfirmWinerQueue: [],
      notComfirmSInt: "",
      isRoomListLoading: false,
      stInt: "",
      isShowMain: false,
      gameURL: window.pokerEnvironment().gameURL,
      currentRow: "",
      sliderModel: "",
      changeSliderModel: "",
      dialogInfo: {
        dialogVisable: false
      },
      tipDialogInfo: {
        dialogVisable: false
      },
      roomDataList: [],
      gameList: [
        {
          label: 'tabItemPoker',
          imgSrc: require("../assets/poker.png")
        },
        {
          label: 'tabItemMore',
          isDeving: true
        }
      ]
    };
  },
  created() {},
  mounted() {
    
    const that = this;

    this.dofetchRoomList();
    window.addEventListener("load", function(event) {
      if (!window.tronWeb) {
      window.setTimeout(function(){
      window.tronAppInt.initRes.then(() => {
        that.dofetchIsonchannel({
          addr: tronAppInt.tronWeb.defaultAddress.hex
        });
        if (that.notComfirmSInt) {
          window.clearInterval(that.notComfirmSInt);
        }
        that.notComfirmSInt = window.setInterval(function() {
          that.dofetchIsonchannel({
            addr: tronAppInt.tronWeb.defaultAddress.hex
          });
        }, 10000);
      });
      },2000);
      } else {
        if (that.notComfirmSInt) {
          window.clearInterval(that.notComfirmSInt);
        }

        that.notComfirmSInt = window.setInterval(function() {
          that.dofetchIsonchannel({
            addr: tronAppInt.tronWeb.defaultAddress.hex
          });
        }, 10000);
      }
    });
    this.stInt = setInterval(() => {
      this.dofetchRoomList();
    }, 3000);
  },
  computed: {
    limitFeeComputed() {
      return this.$t("RoomPage.roomFeeTips", {
        limitFee: numeral(Number(this.currentRow.limitFee)).format("0a"),
        maxFee: numeral(Number(this.currentRow.maxFee)).format("0a")
      });
    }
  },
  methods: {
    reComfirm(confirmData) {},
    beforDialogCLose(done) {
      done();
    },
    dofetchIsonchannel(addr) {
      const that = this;
      fetchIsonchannel(addr)
        .then(response => {
          const { data } = response;
          
             console.log('-----',JSON.stringify(data));
            // if (data.data.notComfirmWinerList.length > 0 || data.data.notFoundList.length > 0) {
                 if (data.data.notComfirmWinerList.length > 0) {
              if (!that.confirmInt) {
                window.clearInterval(that.notComfirmSInt);
                const winNotComfirm = data.data.notComfirmWinerList[0];
                that.winNotComfirmItem = winNotComfirm;
                console.log("", winNotComfirm);
                that.confirmInt = this.$alert(
                  `${this.$t("RoomPage.winnerConfirmMessageTip")}`,
                  `${this.$t("RoomPage.winnerConfirmTitileTip")}`,
                  {
                    confirmButtonText: `${this.$t("NavPage.confirmExchangButtonLabel")}`,
                    beforeClose: async (action, instance, done) => {
                      if (action === "confirm") {
                        that.fullscreenLoading = true
                        try {
                          let state = await tronAppInt.getQGameState(
                            that.winNotComfirmItem.translateGid
                          );
                          
                          if (state == 4) {
                             that.fullscreenLoading = false
                            that.notComfirmSInt = window.setInterval(
                              function() {
                                that.dofetchIsonchannel({
                                  addr: tronAppInt.tronWeb.defaultAddress.hex
                                });
                              },
                              10000
                            );
                            that.$message({
                              message: `${this.$t("RoomPage.winnerConfirmSuccessTip")}`,
                              type: "success"
                            });
                            that.confirmInt = "";
                            done();
                          } else {
                            const winnerRes = await tronAppInt.getQcGameParmFromEndGameInfo(
                              that.winNotComfirmItem
                            );
                            tronAppInt
                              .checkGameVerifyBytes(
                                winnerRes.room.notBets,
                                winnerRes.room.allPots,
                                winnerRes.room.SResult
                              )
                              .then(verifyResult => {
                                console.log("-----verifyResult----");
                                console.log(verifyResult);
                                tronAppInt
                                  .submitQGameResultHelper(winnerRes)
                                  .then(res => {
                                    if (res == true) {
                                      const postParm = Object.assign(
                                        {},
                                        that.winNotComfirmItem
                                      );
                                      console.log(
                                        "",
                                        JSON.stringify(postParm)
                                      );
                                      that.notComfirmSInt = window.setInterval(
                                            function() {
                                              that.dofetchIsonchannel({
                                                addr:
                                                  tronAppInt.tronWeb
                                                    .defaultAddress.hex
                                              });
                                            },
                                            10000
                                          );
                                           that.fullscreenLoading = false
                                          that.$message({
                                            message: `${this.$t("RoomPage.winnerConfirmSuccessTip")}`,
                                            type: "success"
                                          });
                                          that.confirmInt = "";
                                          done();
                                    }else{
                                       that.fullscreenLoading = false
                                       that.notComfirmSInt = window.setInterval(function() {
                                        that.dofetchIsonchannel({
                                          addr: tronAppInt.tronWeb.defaultAddress.hex
                                        });
                                      }, 10000);
                                      that.$message({
                                        message: `${this.$t("RoomPage.winnerConfirmErrorTip")}`,
                                        type: "error"
                                      });
                                    }
                                  }).catch(error=>{
                                that.fullscreenLoading = false
                                       that.notComfirmSInt = window.setInterval(function() {
                                        that.dofetchIsonchannel({
                                          addr: tronAppInt.tronWeb.defaultAddress.hex
                                        });
                                      }, 10000);
                                      that.$message({
                                        message: `${this.$t("RoomPage.winnerConfirmErrorTip")}`,
                                        type: "error"
                                      });
                                      console.error(error);
                              });
                              });
                          }
                        } catch (error) {
                          that.notComfirmSInt = window.setInterval(function() {
                            that.dofetchIsonchannel({
                              addr: tronAppInt.tronWeb.defaultAddress.hex
                            });
                          }, 10000);
                          that.$message({
                            message: `${this.$t("RoomPage.winnerConfirmErrorTip")}`,
                            type: "error"
                          });
                          console.error(error);
                        }
                      } else {
                        that.notComfirmSInt = window.setInterval(function() {
                          that.dofetchIsonchannel({
                            addr: tronAppInt.tronWeb.defaultAddress.hex
                          });
                        }, 10000);
                        console.log("");
                        that.confirmInt = "";
                        done();
                      }
                    }
                  }
                );
              }
            }
            if (data.data.notCofirmList.length > 0) {
              if (!that.confirmInt) {
                window.clearInterval(that.notComfirmSInt);
                const notComfirm = data.data.notCofirmList[0];
                that.notComfirmItem = notComfirm;
                that.confirmInt = this.$alert(
                  `${this.$t("RoomPage.loserConfirmMessageTip")}`,
                  `${this.$t("RoomPage.winnerConfirmTitileTip")}`,
                  {
                    confirmButtonText: `${this.$t("NavPage.confirmExchangButtonLabel")}`,
                    beforeClose: async (action, instance, done) => {
                      if (that.notComfirmSInt) {
                        window.clearInterval(that.notComfirmSInt);
                      }
                      if (action === "confirm") {
                       
                        that.fullscreenLoading = true
                        tronAppInt
                          .getQGameState(that.notComfirmSInt.translateGid)
                          .then(state => {
                            if (state == 4) {
                              that.fullscreenLoading = false
                               that.notComfirmSInt = window.setInterval(
                              function() {
                                that.dofetchIsonchannel({
                                  addr: tronAppInt.tronWeb.defaultAddress.hex
                                });
                              },
                              10000
                            );
                              that.$message({
                                message: `${this.$t("RoomPage.winnerConfirmSuccessTip")}`,
                                type: "success"
                              });
                              that.confirmInt = "";
                              done();
                            } else {
                              tronAppInt
                                .addContractNeedDataFromGameInfo(
                                  that.notComfirmItem
                                )
                                .then(transformData => {
                                  tronAppInt
                                    .checkGameVerifyBytes(
                                      transformData.room.notBets,
                                      transformData.room.allPots,
                                      transformData.room.SResult
                                    )
                                    .then(verifyResult => {
                                      console.log("-----verifyResult----");
                                      console.log(verifyResult);
                                      tronAppInt
                                        .setQGameResultNotWinner(
                                          tronAppInt.roomID2GameID(
                                            ~~transformData.room.id,
                                            transformData.room.gid
                                          ),
                                          transformData.room.SSeed,
                                          transformData.room.winner,
                                          transformData.room.notBets,
                                          transformData.room.allPots,
                                          transformData.room.SResult
                                        )
                                        .then(noWinnerRes => {
                                          if(noWinnerRes == true){
                                            console.log("------", noWinnerRes);
                                          const postParm = Object.assign(
                                            {},
                                            transformData
                                          );
                                          that.notComfirmSInt = window.setInterval(
                                              function() {
                                                that.dofetchIsonchannel({
                                                  addr:
                                                    tronAppInt.tronWeb
                                                      .defaultAddress.hex
                                                });
                                              },
                                              10000
                                            );
                                             that.fullscreenLoading = false
                                            that.$message({
                                              message: `${this.$t("RoomPage.winnerConfirmSuccessTip")}`,
                                              type: "success"
                                            });
                                            that.confirmInt = "";
                                            done();
                                          }else{
                                            
                                             that.fullscreenLoading = false
                                            that.notComfirmSInt = window.setInterval(
                                            function() {
                                              that.dofetchIsonchannel({
                                                addr:
                                                  tronAppInt.tronWeb
                                                    .defaultAddress.hex
                                              });
                                            },
                                            10000
                                          );
                                          that.$message({
                                            message: `${this.$t("RoomPage.winnerConfirmErrorTip")}`,
                                            type: "error"
                                          });
                                          }
                                        })
                                        .catch(e => {
                                           that.fullscreenLoading = false
                                          that.notComfirmSInt = window.setInterval(
                                            function() {
                                              that.dofetchIsonchannel({
                                                addr:
                                                  tronAppInt.tronWeb
                                                    .defaultAddress.hex
                                              });
                                            },
                                            10000
                                          );
                                          console.error(e);
                                          that.$message({
                                            message: `${this.$t("RoomPage.winnerConfirmErrorTip")}`,
                                            type: "error"
                                          });
                                        }).catch(error=>{
                                that.fullscreenLoading = false
                                       that.notComfirmSInt = window.setInterval(function() {
                                        that.dofetchIsonchannel({
                                          addr: tronAppInt.tronWeb.defaultAddress.hex
                                        });
                                      }, 10000);
                                      that.$message({
                                        message: `${this.$t("RoomPage.winnerConfirmErrorTip")}`,
                                        type: "error"
                                      });
                                      console.error(error);
                              });
                                    });
                                });
                            }
                          });
                      } else {
                        that.notComfirmSInt = window.setInterval(function() {
                          that.dofetchIsonchannel({
                            addr: tronAppInt.tronWeb.defaultAddress.hex
                          });
                        }, 10000);
                        console.log("");
                        that.confirmInt = "";
                        done();
                      }
                    }
                  }
                );
              }
            }
            if(data.data.onlyOnePlayerList.length > 0){
              if (!that.confirmInt) {
                window.clearInterval(that.notComfirmSInt);
                const notComfirm = data.data.onlyOnePlayerList[0];
                that.notComfirmItem = notComfirm;
                that.confirmInt = this.$alert(
                  `${this.$t("RoomPage.loserConfirmMessageTip")}`,
                  `${this.$t("RoomPage.winnerConfirmTitileTip")}`,
                  {
                    confirmButtonText: `${this.$t("NavPage.confirmExchangButtonLabel")}`,
                    beforeClose: async (action, instance, done) => {
                      if (that.notComfirmSInt) {
                        window.clearInterval(that.notComfirmSInt);
                      }
                      if (action === "confirm") {
                       
                        that.fullscreenLoading = true
                        tronAppInt
                          .giveBackOnlyOnePlayerGameChips(that.notComfirmItem.translateGid)
                          .then(state => {
                           
                            if (state) {
                              that.fullscreenLoading = false
                               that.notComfirmSInt = window.setInterval(
                              function() {
                                that.dofetchIsonchannel({
                                  addr: tronAppInt.tronWeb.defaultAddress.hex
                                });
                              },
                              10000
                            );
                              that.$message({
                                message: `${this.$t("RoomPage.winnerConfirmSuccessTip")}`,
                                type: "success"
                              });
                              that.confirmInt = "";
                              done();
                            } else {
                              that.fullscreenLoading = false
                                       that.notComfirmSInt = window.setInterval(function() {
                                        that.dofetchIsonchannel({
                                          addr: tronAppInt.tronWeb.defaultAddress.hex
                                        });
                                      }, 10000);
                                      that.$message({
                                        message: `${this.$t("RoomPage.winnerConfirmErrorTip")}`,
                                        type: "error"
                                      });
                            }
                          });
                      } else {
                        that.notComfirmSInt = window.setInterval(function() {
                          that.dofetchIsonchannel({
                            addr: tronAppInt.tronWeb.defaultAddress.hex
                          });
                        }, 10000);
                        console.log("");
                        that.confirmInt = "";
                        done();
                      }
                    }
                  }
                );
              }
            }
        })
        .catch(error => {
          console.error(error)
          this.$message({ message: error, type: "error" });
        });
    },
    getTRXBalance() {
      return window.tronAppInt.getTRXBalance();
    },
    usrInfoSave() {},
    notEnoughtSavehandel() {
      this.tipDialogInfo.dialogVisable = false;
      this.$store.dispatch("setBuyBalanceVisable", true);
    },
    savehandel:async function () {
      if (
        ~~this.changeSliderModel >
        ~~this.$store.state.userInfo.usrInfo.usrBalance
      ) {
        this.$confirm(`${this.$t("RoomPage.lessFeeDialogTitle")}`, `${this.$t("NavPage.confirmExchangTitle")}`, {
          confirmButtonText: `${this.$t("NavPage.roomFeeBuy")}`,
          cancelButtonText: `${this.$t("NavPage.cancelExchangButtonLabel")}`,
          type: "warning"
        })
          .then(() => {
            this.$store.dispatch("setBuyBalanceVisable", true);
            this.dialogInfo.dialogVisable = false;
          })
          .catch(() => {});
      } else {
        this.dialogInfo.dialogVisable = false;
        let authtk = localStorage.getItem("usertk");
        let userId = tronAppInt.tronWeb.defaultAddress.hex;
        if (authtk && authtk.startsWith(userId)) {
          authtk = authtk.substr(userId.length);
        } else {
          authtk = "";
        }

        if (authtk) {
          const checkRes = await dochecktoken({
            token:authtk,
            innerAddrs:tronAppInt.tronWeb.defaultAddress.hex
          })
          const isSuccess = checkRes.data.data
         
            if(isSuccess){
                this.joinRoom(
                  this.currentRow.roomId,
                  tronAppInt.tronWeb.defaultAddress.hex,
                  this.$store.state.userInfo.usrInfo.nickname,
                  this.$store.state.userInfo.usrInfo.portrait,
                  this.$store.state.userInfo.usrInfo.email,
                  ~~this.changeSliderModel,
                  authtk
                );
                return;
            }
        }
        fetchAuthentication({
          curTime: new Date().valueOf(),
          innerAddrs: tronAppInt.tronWeb.defaultAddress.hex
        })
          .then(data => {
            tronAppInt.userSignWrapped(data.data.data).then(v => {
              fetchSignSeed({
                hexSignResult: v,
                innerAddrs: tronAppInt.tronWeb.defaultAddress.hex
              })
                .then(res => {

                  this.joinRoom(
                    this.currentRow.roomId,
                    tronAppInt.tronWeb.defaultAddress.hex,
                    this.$store.state.userInfo.usrInfo.nickname,
                    this.$store.state.userInfo.usrInfo.portrait,
                    this.$store.state.userInfo.usrInfo.email,
                    ~~this.changeSliderModel,
                    res.data.data
                  );
                  localStorage.setItem(
                    "usertk",
                    tronAppInt.tronWeb.defaultAddress.hex + res.data.data
                  );
                })
                .catch(error => {
                  console.error(error)
                  this.$message({ message: error, type: "error" });
                });
            });
          })
          .catch(error => {
            console.error(error)
            this.$message({ message: error, type: "error" });
          });
      }
    },
    joinRoom(roomID, userID, userName, portrait, email, chips, token) {
      window.pokerRoomParm = {
        roomID:roomID.toString(), userID, userName, portrait:portrait.toString(), email,token,chips:chips.toString()
      }
      console.log('join room parms')
      console.log(JSON.stringify( window.pokerRoomParm))
      this.$router.push({
        name: "pokerroom",
        query: {
          roomID,
        }
      });
    },
    rowClickHandle(row, event, column) {
     
      this.currentRow = row;
      if(Number(this.currentRow.maxFee) >= Number(this.$store.state.userInfo.usrInfo.usrBalance)){
        this.changeSliderModel = Number(this.$store.state.userInfo.usrInfo.usrBalance)
      }else{
        this.changeSliderModel = Number(this.currentRow.maxFee)
      }
      if (
        ~~this.$store.state.userInfo.usrInfo.usrBalance >=
        ~~this.currentRow.limitFee
      ) {
        this.dialogInfo.dialogVisable = true;
      } else {
        this.tipDialogInfo.dialogVisable = true;
      }
    },
    dofetchRoomList(id) {
      let idObj = id ? { id } : "";
      fetchRoomList(idObj)
        .then(response => {
          const { data } = response;
          if (data) {
            this.roomDataList = data.data;
          }
        })
        .catch(error => {
          console.error(error)
          this.$message({ message: error, type: "error" });
        });
    },

    getCookie(name) {
      let arr,
        reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");

      if ((arr = document.cookie.match(reg))) return decodeURI(arr[2]);
      else return null;
    },
    setCookie(name, value) {
      let Days = 30;
      let exp = new Date();
      exp.setTime(exp.getTime() + Days * 24 * 60 * 60 * 1000);
      let str =
        name + "=" + encodeURI(value) + "; expires=" + exp.toGMTString();
      document.cookie = str;
    }
  },
  beforeDestroy() {
    if (this.stInt) {
      clearInterval(this.stInt);
    }
    if (this.notComfirmSInt) {
      clearInterval(this.notComfirmSInt);
    }
  }
};
</script>
