  color: rgba(255, 255, 255, 0.76);
}
#roomList .el-table__body-wrapper tr {
  height: 59px;
  font-family: PingFangSC-Regular;
  font-size: 16px;
  background-color: #5d2da5;
  color: rgba(204, 204, 204, 0.76);
  cursor: pointer;
}
.el-table--enable-row-hover .el-table__body tr:hover > td {
  background-color: #883aec;
}
#roomList .el-table--group::after,
.el-table--border::after,
.el-table::before {
  background: none;
}
</style>
<style>
.el-table th.is-leaf,
.el-table td {
  border-bottom: 0.01818rem solid rgba(235, 238, 245, 0.1);
}
#roomInfoModel .el-dialog__body,
#lessLimitInfoModel .el-dialog__body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#roomInfoModel .el-input-number,
#lessLimitInfoModel .el-input-number {
  width: 100%;
  height: 75px;
}

#roomInfoModel .el-input-number .el-input-number__decrease,
#lessLimitInfoModel .el-input-number .el-input-number__decrease {
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.76);
}

#roomInfoModel .el-input-number .el-input-number__increase,
#lessLimitInfoModel .el-input-number .el-input-number__increase {
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.76);
}
#roomInfoModel .el-dialog,
#lessLimitInfoModel .el-dialog {
  width: 770px;
  height: 70%;
  background-color: #33304e;
  border-radius: 6px;
  padding: 0.1rem 0.1rem;
}
#roomInfoModel .portraitPhotos,
#lessLimitInfoModel .portraitPhotos {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  margin-bottom: 30px;
}
#roomInfoModel .el-dialog .selectBox,
#lessLimitInfoModel .el-dialog .selectBox {
  width: 60px;
  height: 60px;
  margin-right: 4px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#roomInfoModel .selectedImg,
#lessLimitInfoModel .selectedImg {
  background-image: linear-gradient(
    79deg,
    #b84889 0%,
    #bb6062 50%,
    #bd783a 100%
  );
}
#roomInfoModel .el-dialog img,
#lessLimitInfoModel .el-dialog img {
  width: 50px;
  height: 50px;
  background-color: #33304e;
  border-radius: 50%;
  cursor: pointer;
  border: 3px solid #33304e;
}
#roomInfoModel .el-dialog__title,
#lessLimitInfoModel .el-dialog__title {
  font-family: NotoSansCJKsc-Regular;
  font-size: 16px;
  color: rgba(255, 255, 255, 0.76);
}
#roomInfoModel.el-dialog__header,
#lessLimitInfoModel .el-dialog__header {
  text-align: center;
}
#roomInfoModel input.el-input__inner,
#lessLimitInfoModel input.el-input__inner {
  background-color: #27253f;
  border: none;
  margin-bottom: 0.07273rem;
  color: rgba(255, 255, 255, 0.76);
}
#roomInfoModel input.el-input__inner::-webkit-input-placeholder,
#lessLimitInfoModel input.el-input__inner::-webkit-input-placeholder {
  font-family: NotoSansCJKsc-Regular;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  color: #8a84be;
}
#roomInfoModel .usrInfoSave,
#lessLimitInfoModel .usrInfoSave {
  width: 50%;
  height: 54px;
  background-image: linear-gradient(
    79deg,
    #b84889 0%,
    #bb6062 50%,
    #bd783a 100%
  );
  box-shadow: 0px 6px 15px 0px rgba(31, 28, 58, 0.1);
  border-radius: 12rem;
  margin: 30px 0px;
  border: none;
  color: rgba(255, 255, 255, 0.76);
}
#roomInfoModel .userInfo,
#lessLimitInfoModel .userInfo {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}
#roomInfoModel .dialog-footer,
#lessLimitInfoModel .dialog-footer {
  align-self: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
</style>



