<template>
  <section :class="['Nav',!isShowLogo?'innerNav':'']">
    <el-row type="flex" justify="space-between" align="flex-start">
      <el-col :span="15">
        <section class="userinfo">
          <div class="userportraitWrapper">
            <img
              src="../assets/usricon.png"
              class="userportrait"
              alt="portrait"
              @click="usrSettingHandle"
            >
          </div>
          <div v-if="isLogin" class="userSymbol">
            <span class="userToken">{{usrNicknameTip}} {{visAddr}}</span>
            <section class="balanceBox">
              <img src="../assets/balance.png" alt="balance">
              <span class="balance">{{selfInfo.usrBalance}} {{$t('NavPage.balance')}}</span>
            </section>
            <section>
              <el-button
                class="buyChip"
                @click="changeBalanceDialogHandle(1)"
              >{{$t('NavPage.buyChipTip')}}</el-button>
              <el-button
                class="buyChip"
                @click="changeBalanceDialogHandle(2)"
              >{{$t('NavPage.withdrawTip')}}</el-button>
            </section>
          </div>
          <div v-else class="userSymbol">
            <el-button type="text" @click="loginHandle" class="userToken">{{$t('NavPage.loginTip')}}</el-button>
          </div>
        </section>
      </el-col>
      <!-- <el-col :span="2">
        <section>
          <img v-show="!isShowLogo" src="../assets/logo.png" style="width: 154px;height: 65px;" alt>
        </section>
      </el-col> -->

      <span type="text" @click="handleWhitepage" style="color:white;cursor: pointer;">{{$t('NavPage.withePage')}}</span>
      <span type="text" @click="handleOfficalNet" style="color:white;cursor: pointer;">{{$t('NavPage.officalNet')}}</span>
      <el-col :span="5" class="countrySelect">
        <el-dropdown trigger="click" @command="handleCommand">
          <span class="el-dropdown-link">
            <span class="selectedCountry">{{countrySelected}}</span>
            <i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="zhCHS">
              <img src="../assets/ch.png" alt style="width: 16px;height: 16px;vertical-align: sub;">
              <span>{{$t('NavPage.country.zhCHS')}}</span>
            </el-dropdown-item>
            <el-dropdown-item command="en">
              <img src="../assets/en.png" alt style="width: 16px;height: 16px;vertical-align: sub;">
              <span>{{$t('NavPage.country.en')}}</span>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-col>
    </el-row>
    <section style="display:flex;justify-content:center;">
      <img v-show="isShowLogo" src="../assets/logo.png" class="logoStyle" alt>
    </section>

    <!--  -->
    <el-dialog
      id="userInfoModel"
      width="50%"
      class="userInfoDialog"
      :title="dialogInfo.dialogTitle"
      :visible.sync="dialogInfo.dialogVisable"
      append-to-body
    >
      <el-form :model="userInfoModel" class="usrModel">
        <div width="100px"></div>
        <section class="userInfo">
          <section class="portraitPhotos">
            <div
              v-for="(PortItem,PortIdx) of portraits"
              :key="PortIdx"
              :class="['selectBox',Number(userSelectedImgDialog) === Number(PortItem.pId) ? 'selectedImg':'']"
              @click="handleSelectedImg(PortItem.pId)"
            >
              <img :src="PortItem.isrc" alt="PortItem.label" class="usrPortraitImg">
            </div>
          </section>
          <section>
            <span>{{usrNicknameChangeTip}}</span>
            <el-button
              @click="radomNickNameHandle"
              type="text"
              style="padding-left:10px;"
            >{{$t('NavPage.radomNickName')}}</el-button>
          </section>
          <el-input
            v-model="usrNicknameChangeTip"
            :maxlength="5"
            :placeholder="$t('NavPage.fillRequiredTip')"
          ></el-input>
          <span slot="footer" class="dialog-footer">
            <el-button
              round
              class="usrInfoSave"
              @click="savehandel"
            >{{this.$t('NavPage.userInfo.saveBtn')}}</el-button>
          </span>
        </section>
      </el-form>
    </el-dialog>
    <!--  -->
    <el-dialog
      id="balanceModel"
      width="50%"
      class="balanceChangeDialog"
      :title="dialogBalance.dialogTitle"
      :visible.sync="$store.state.userInfo.buyBalanceVisable"
      append-to-body
      :before-close="beforDialogCLose"
    >
      <section class="changeBalanceBox">
        <img src="../assets/balance.png" alt="balance">
        <section style="padding-bottom:10px;">
          <span class="rate">{{$t('NavPage.rateTip')}}</span>
          <span class="rate" style="padding-left:23px;">{{$t('NavPage.rateEndTip')}}</span>
        </section>
        <span style="padding-bottom:20px;">
          {{$t('NavPage.currentBalanceTip',{
          trxBalance:selfInfo.usrTRXBalance,
          balance:selfInfo.usrBalance
          })}}
        </span>
        <el-slider
          v-model="changeBalenceModel.value"
          :min="changeBalenceModel.min"
          :max="changeBalenceModel.max"
          :step="changeBalenceModel.step"
          show-input
          class="balanceSlider"
        ></el-slider>
        <span class="changingTipBox">
          {{$t('NavPage.changingbalance')}}
          <span class="changingTip">{{changeBalenceModel.value}}</span>
          {{curBalanceType == 1?$t('NavPage.balance'):$t('NavPage.trxBalance')}}
        </span>
        <el-button class="changeBtn" @click="changeHandle">{{$t('NavPage.changeBtnTip')}}</el-button>
      </section>
    </el-dialog>
  </section>
</template>
<script>
import fakerName from "../../src/api/fakerName";
import MenuIcon from "../components/MenuIcon";
import TronPokerApp from "../lib/tron";
import {
  changeBalance,
  changeTRXBalance,
  fetchUsrInfo,
  saveUserInfo
} from "@/api/poker.js";
export default {
  name: "Nav",
  components: {
    MenuIcon
  },
  data() {
    return {
      selfInfoSInt: "",
      notComfirmSInt: "",
      curWidth: document.body.clientWidth,
      isMobileViw: false,
      curBalanceType: 1,
      selectChangeType: "",
      selectChangeOptions: [
        {
          value: 0,
          label: this.$t("NavPage.selectOpt0")
        },
        {
          value: 1,
          label: this.$t("NavPage.selectOpt1")
        }
      ],
      selfInfo: {
        usrToken: this.$t("NavPage.loadingTip"),
        usrTRXBalance: "",
        usrBalance: this.$t("NavPage.loadingTip"),
        nickname: "",
        email: "",
        portrait: "",
        usrAuthToken: "",
        _id: ""
      },
      userInfoModel: {},
      changeBalenceModel: {
        min: 100,
        max: 10000,
        step: 10,
        value: 100
      },
      userSelectedImgId: "",
      usrNicknameChangeTip: "",
      usrEmailDialog: "",
      userSelectedImgDialog: "",
      usrNicknameTip: "",
      usrEmailTip: "",
      isLogin: true,
      dialogInfo: {
        dialogVisable: false,
        dialogTitle: this.$t("NavPage.userInfo.modelTitle")
      },
      dialogBalance: {
        dialogVisable: false
      },
      countrySelected: window.usrLang
        ? this.$t(`NavPage.country.${window.usrLang}`)
        : this.$t(`NavPage.country.zhCHS`),
      navList: {
        logoLabel: this.$t("NavPage.hello"),
        logoImgSrc: require("../assets/logo.png"),
        selfInfo: {
          token: "",
          symbolCount: ""
        }
      },
      visAddr: "loading...",
      portraits: [
        { pId: 1, label: "en", isrc: require("../assets/en.png") },
        { pId: 2, label: "ch", isrc: require("../assets/ch.png") },
        { pId: 3, label: "en", isrc: require("../assets/en.png") },
        { pId: 4, label: "ch", isrc: require("../assets/ch.png") },
        { pId: 5, label: "en", isrc: require("../assets/en.png") },
        { pId: 6, label: "ch", isrc: require("../assets/ch.png") }
      ],
      langList: [
        {
          label: "cn"
        },
        {
          label: "en"
        }
      ],
      imgUpload: {}
    };
  },
  props: {
    isShowLogo: {
      type: Boolean,
      default: true
    }
  },
  created() {
    const that = this;
    // window.addEventListener("beforeunload", function(e) {
    //   e = e || window.event;
    //   // IE8Firefox 4
    //   if (e) {
    //     e.returnValue = window.pokerLang.tips.existTip;
    //   }
    //   // Chrome, Safari, Firefox 4+, Opera 12+ , IE 9+
    // });
  },
  mounted() {
    const that = this;

    const appConf = {
      //mainContract: "41a900d47528ea354ed4b15e5ce20944f14dcc3e61"
      mainContract: "41fd2081ac97fc85102d9f9d7b2b3703a901618185"
    };
    that.afterTronWeb = function(tronWeb) {
      const tronAppInt = new TronPokerApp(tronWeb, appConf.mainContract);
      window.tronAppInt = tronAppInt;
      var tmpf = function() {
        window.tronAppInt
          .waitChipToken()
          .then(() => {
            that.getSelfInfo();
            that.selfInfoSInt = window.setInterval(() => {
              that.getSelfInfo();
            }, 10000);
          })
          .catch(e => {
            tronAppInt.initAllContract(appConf.mainContract);
            tmpf();
          });
      };
      tmpf();
    };
    that.initTronWeb = function() {
      return new Promise(function(resolve, reject) {
        let tries = 0;
        const loadFinish = function() {
          resolve(window.tronWeb);
        };
        let timer = setInterval(function() {
          if (window.tronWeb) {
            clearInterval(timer);
            if (!window.tronWeb.defaultAddress.base58) {
              window.tronWeb.on("addressChanged", function() {
                return loadFinish();
              });
            } else {
              return loadFinish();
            }
          }
          if (tries > 20) {
            clearInterval(timer);
            reject();
          }
        }, 100);
      });
    };
    window.addEventListener("resize", function(event) {
      that.curWidth = document.body.clientWidth;
    });
    window.addEventListener("load", function(event) {
      that
        .initTronWeb()
        .then(that.afterTronWeb)
        .catch(e => {
          // that.$message.error(e);
        });
    });
    if (window.tronWeb) {
      that.getSelfInfo();
      that.selfInfoSInt = window.setInterval(() => {
        that.getSelfInfo();
      }, 10000);
    }
  },
  computed: {
    changeTip: function() {
      return `${this.$t("NavPage.changingbalance")} ${
        this.changeBalenceModel.value
      } ${
        this.curBalanceType == 1
          ? this.$t("NavPage.balance")
          : this.$t("NavPage.trxBalance")
      }`;
    }
  },
  beforeUpdate() {},
  methods: {
    handleWhitepage() {
      window.location.href = this.$t("NavPage.withePageURL");
    },
    handleOfficalNet() {
      window.location.href = this.$t("NavPage.officalNetURL");
    },
    radomNickNameHandle() {
      this.usrNicknameChangeTip = fakerName(this.$i18n.locale).substr(0, 5);
    },
    usrSettingHandle() {
      this.usrNicknameChangeTip = this.usrNicknameTip;
      this.usrEmailDialog = this.usrEmailTip;
      this.userSelectedImgDialog = this.userSelectedImgId;
      this.dialogInfo.dialogVisable = true;
    },
    beforDialogCLose(done) {
      this.changeBalenceModel.value = 100;

      done();
    },
    changeHandle() {
      if(!tronAppInt.getBalance){
        
        this.$message({
        message: `${this.$t("NavPage.loadingErrorBalanceTip")}`,
        type: "error"
      });
      return;
      }
      const that = this;

      this.$confirm(
        `${this.changeTip}`,
        `${this.$t("NavPage.confirmExchangTitle")}`,
        {
          confirmButtonText: `${this.$t("NavPage.confirmExchangButtonLabel")}`,
          cancelButtonText: `${this.$t("NavPage.cancelExchangButtonLabel")}`,
          type: "warning"
        }
      ).then(() => {
        const loading = this.$loading({
          lock: true,
          text: "Loading",
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.7)"
        });
        switch (Number(that.curBalanceType)) {
          case 1:
            window.tronAppInt
              .deposit(that.changeBalenceModel.value * Math.pow(10, 6))
              .then(res => {
                loading.close();
                this.getSelfInfo();
                this.$store.dispatch("setBuyBalanceVisable", false);
                this.$message({
                  message: `${this.$t("NavPage.confirmExchangSuccess")}`,
                  type: "success"
                });
              })
              .catch(e => {
                loading.close();
                this.getSelfInfo();
                this.$store.dispatch("setBuyBalanceVisable", false);
                console.error(e)
                this.$message({
                  message: `${this.$t("NavPage.confirmExchangFail")}`,
                  type: "error"
                });
              });
            break;
          case 2:
            window.tronAppInt
              .withdraw(that.changeBalenceModel.value * Math.pow(10, 6))
              .then(res => {
                loading.close();
                this.getSelfInfo();
                this.$store.dispatch("setBuyBalanceVisable", false);
                this.$message({
                  message: `${this.$t("NavPage.confirmExchangSuccess")}`,
                  type: "success"
                });
              })
              .catch(e => {
                loading.close();
                this.getSelfInfo();
                this.$store.dispatch("setBuyBalanceVisable", false);
                console.error(e)
                this.$message({
                  message: `${this.$t("NavPage.confirmExchangFail")}`,
                  type: "error"
                });
              });
            break;
        }
      });
    },
    changeBalanceDialogHandle(type) {
      this.curBalanceType = Number(type);
      this.$store.dispatch("setBuyBalanceVisable", true);
      
      switch (Number(type)) {
        case 1:
          this.dialogBalance.dialogTitle = this.$t(
            "NavPage.balanceChangeTitleTip"
          );
          this.changeBalenceModel.max = Number(this.selfInfo.usrTRXBalance)
           
          break;
        case 2:
          this.dialogBalance.dialogTitle = this.$t(
            "NavPage.withDrawBalanceChangeTitleTip"
          );
           this.changeBalenceModel.max = Number(this.selfInfo.usrBalance)
           this.changeBalenceModel.min = Number(this.selfInfo.usrBalance)
          
           
          break;
      }
    },
    getSelfInfo: async function() {
      try {
        this.visAddr =
          tronWeb.defaultAddress.base58.substr(0, 4) +
          "*****" +
          tronWeb.defaultAddress.base58.substr(
            tronWeb.defaultAddress.base58.length - 4
          );
        this.selfInfo.usrToken = await window.tronWeb.defaultAddress.hex;
        const [bigBalance, trxBalance] = await Promise.all([
          window.tronAppInt.getBalance(),
          window.tronAppInt.getTRXBalance()
        ]);
        let userInfo = ""
        if (!this.selfInfo.nickname) {
          let usrRes = "";
          usrRes = await fetchUsrInfo({ token: this.selfInfo.usrToken });
          userInfo = usrRes.data.data;
          
        } else {
          userInfo = this.selfInfo;
        }
        let authtk = localStorage.getItem("usertk");

        if (authtk && authtk.startsWith(this.selfInfo.usrToken)) {
          authtk = authtk.substr(this.selfInfo.usrToken.length);
        } else {
          authtk = "";
        }
        this.selfInfo = Object.assign({}, this.selfInfo, {
          nickname: userInfo.nickname,
          email: userInfo.email,
          portrait: userInfo.portrait,
          usrBalance: Math.floor(window.tronWeb.fromSun(bigBalance)),
          usrTRXBalance: Math.floor(window.tronWeb.fromSun(trxBalance)),
          _id: userInfo._id,
          isGameing: false,
          usrAuthToken: authtk
        });
        //if (authtk) {

        this.$store.dispatch("setUrsInfo", this.selfInfo);
        //}
        this.usrNicknameTip = userInfo.nickname;
        this.usrEmailTip = userInfo.email;
        this.userSelectedImgId = userInfo.portrait;
      } catch (e) {}
    },
    checkFiled() {
      if (!this.userSelectedImgDialog) {
        this.$message({
          message: `${this.$t("NavPage.selectportraitTip")}`,
          type: "warning"
        });
        return false;
      }
      if (!this.usrNicknameTip) {
        this.$message({
          message: `${this.$t("NavPage.fillRequiredTip")}`,
          type: "warning"
        });
        return false;
      }
      // if (
      //   !this.usrEmailDialog.match(
      //     /^([0-9A-Za-z\-_\.]+)@([0-9a-z]+\.[a-z]{2,3}(\.[a-z]{2})?)$/g
      //   )
      // ) {
      //   this.$message({
      //     message: "",
      //     type: "warning"
      //   });
      //   return false;
      // }
      if (this.usrNicknameChangeTip.length > 5) {
        this.$message({
          message: `${this.$t("NavPage.overlengthTip")}`,
          type: "warning"
        });
        return false;
      }
      return true;
    },
    usrNickNameHandle() {
      if (this.usrNicknameChangeTip.length > 5) {
        this.$message({
          message: `${this.$t("NavPage.overlengthTip")}`,
          type: "warning"
        });
      }
      this.usrNicknameChangeTip = this.usrNicknameChangeTip.substring(0, 5);
    },
    savehandel() {
      if (this.checkFiled()) {
        const loading = this.$loading({
          lock: true,
          text: "Loading",
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.7)"
        });
        //FIXME: this api need check user auth token.
        saveUserInfo({
          id: this.selfInfo._id,
          token: this.selfInfo.usrToken,
          email: this.usrEmailDialog,
          nickname: this.usrNicknameChangeTip?this.usrNicknameChangeTip:fakerName().substr(0, 5),
          portrait: this.userSelectedImgDialog,
          isGameing: false
        })
          .then(res => {
            loading.close();
            this.usrNicknameTip = this.usrNicknameChangeTip;
            this.selfInfo = Object.assign({}, this.selfInfo, {
              nickname: this.usrNicknameChangeTip,
              email: this.usrEmailDialog,
              portrait: this.userSelectedImgDialog
            });
            this.$store.dispatch("setUrsInfo", this.selfInfo);
            this.usrEmailTip = this.usrEmailDialog;
            this.userSelectedImgId = this.userSelectedImgDialog;
            this.dialogInfo.dialogVisable = false;
            this.$message({
              message: `${this.$t("NavPage.settingSuccessTip")}`,
              type: "success"
            });
          })
          .catch(e => {
            console.error(e)
            loading.close();
            this.$message({
              message: `${this.$t("NavPage.settinErrorTip")}`,
              type: "error"
            });
          });
      }
    },
    handleSelectedImg(pId) {
      this.userSelectedImgDialog = pId;
    },
    handleCommand(command) {
      this.$i18n.locale = command;
      var translate = `NavPage.country.${command}`;
      this.countrySelected = this.$t(translate);
      window.pokerLang = window.pokerLocalLang[command];
      window.usrLang = command;
    },
    loginHandle() {
      this.dialogInfo.dialogVisable = true;
    },
    handlePictureCardPreview() {},
    handleRemove() {}
  }
};
</script>
