  );
  box-shadow: 0px 6px 15px 0px rgba(31, 28, 58, 0.1);
  border-radius: 12rem;
  margin: 30px 0px;
  border: none;
  color: #ffffff;
}
#userInfoModel .userInfo,
#balanceModel .userInfo {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}
#userInfoModel .dialog-footer,
#balanceModel .dialog-footer {
