}
#balanceModel .changeBalanceBox img {
  width: 20px;
  height: 20px;
  position: absolute;
  top: -6px;
  left: 50%;
}
#balanceModel .changeBalanceBox .changingTipBox {
  margin-top: 30px;
}
#balanceModel .changeBalanceBox .changingTip {
  color: #fee688;
