}
#balanceModel .el-dialog__title {
  font-family: NotoSansCJKsc-Regular;
  font-size: 22px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0px;
  color: #ffffff;
}
#balanceModel .rate {
}
#balanceModel .changeBtn {
  margin-top: 60px;
  width: 50%;
  height: 40px;
  background-image: linear-gradient(
    79deg,
    #b84889 0%,
    #bb6062 50%,
    #bd783a 100%
  );
  box-shadow: 0px 6px 15px 0px rgba(31, 28, 58, 0.1);
  border-radius: 12rem;
  border: none;
  color: #ffffff;
}
#balanceModel .el-input-number__decrease,
#balanceModel .el-input-number__increase {
  background: #26243e;
  border: none;
}
#balanceModel .el-slider__input .el-input__inner {
  color: #fee688;
}
.el-loading-spinner {
  font-size: 44px;
}
</style>



