

function radomName(type){
    let rs = ''
    if(type == 'en'){
         rs = ['Lindgren','Rasheed','Ratke','Osbaldo','Bayer','Christa','Cleora','Filomena']
    }else{
       rs = ['','','V','','','','','']
    }
    
    return rs[Math.floor(Math.random()*5+1)]
}
module.exports = function(isEN){
    return radomName(isEN).substr(0,6)
}



