/**
 * poker
 */

import request from '@/api/request.js'

/**
 * GET 
 */
export function fetchRoomList(query) {
  return request({
    url: '/rooms',
    method: 'get',
    params: query
  })
}
/**
 * post 
 */
export function fetchIsonchannel(data) {
  return request({
    url: '/isonchannel',
    method: 'post',
    data:data
  })
}

