/**
 * POST TRX  
 */
export function changeBalance(data) {
  return request({
    url: '/balance',
    method: 'post',
    data
  })
}
/**
 * POST   TRX
 */
export function changeTRXBalance(data) {
  return request({
    url: '/trxbalance',
    method: 'post',
    data
  })
}
